<template>
  <div>
    <el-row type="flex" justify="left" align="middle" >
      <el-col :span="15"><h1>问卷系统</h1></el-col>
      <el-col :span="5" ><el-input v-model="questionnaireName" placeholder="问卷模板名称"></el-input></el-col>
      <el-col :span="4" :offset="1">
        <el-select v-model="questionnaireTemplateId" placeholder="选择模板">
          <el-option
            v-for="item in this.QuestionnaireTemplate.list"
            :key="item._id"
            :label="item.title"
            :value="item._id">
          </el-option>
        </el-select>
      </el-col>
      <el-col :span="2" :offset="1"><el-button type="primary" size="large" style="postio" @click="add">添加问卷</el-button></el-col>
    </el-row>
    <!-- <el-row class="editRow" type="flex" align="middle" >
      <el-col :span="4" style="font-size: 10px; color:red; "></el-col>
    </el-row> -->

    <el-table
      :data="this.QuestionnaireEach.list"
      border
      style="width: 100%">
      <el-table-column
        label="名称"
        width="300">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.title }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="创建日期"
        width="180">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.operation.dateString }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="操作人"
        width="150">
        <template scope="scope">
          <span style="margin-left: 10px">{{ scope.row.operation.userName }}</span>
        </template>
      </el-table-column>
      <!-- <el-table-column
        label="状态"
        width="120">
        <template scope="scope">
          <el-switch
            v-model="scope.row.status"
            on-color="#13ce66"
            off-color="#ff4949">
          </el-switch>
          <span style="margin-left: 10px">{{ scope.row.status ? '开启' : '关闭' }}</span>
        </template>
      </el-table-column> -->
      <el-table-column label="操作">
        <template scope="scope">
          <el-button
            v-if="scope.row.status"
            type="danger"
            size="small"
            @click="changeStatus(scope.row._id, !scope.row.status)">关闭</el-button>
          <el-button
            v-else
            type="primary"
            size="small"
            @click="changeStatus(scope.row._id, !scope.row.status)">开启</el-button>
          <router-link :to="{ path: 'statistics', query: { questionnaireId: scope.row._id, questionnaireName: scope.row.title }}">
            <el-button
              size="small">统计</el-button>
          </router-link>
          <router-link :to="{ path: `questionnaire/${scope.row._id}`, query: { uid }}">
            <el-button
              size="small">查看</el-button>
          </router-link>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
  // import _ from 'lodash';
  import { mapActions, mapState } from 'vuex';

  export default {
    name: 'QuestionList',
    data(){
      return {
        questionnaireName: '',
        questionnaireTemplateId: '',
        uid: ''
      }
    },
    methods: {
      ...mapActions(['getQuestionnaireEachList', 'getQuestionnaireTemplateList', 'addQuestionnaire', 'changeQuestionnaireEachStatus']),
      init() {
        console.log(this.$route.query.uid, 'this.$route.query.uid');
        this.uid = this.$route.query.uid;
        this.getQuestionnaireEachList();
        this.getQuestionnaireTemplateList();

      },
      add() {
        this.addQuestionnaire({title: this.questionnaireName, templateId: this.questionnaireTemplateId}).then(()=> {
          this.getQuestionnaireEachList();
          this.$notify({
            title: '成功',
            message: '问卷添加成功',
            type: 'success'
          });
        });
        // console.log(this.questionnaireName, 'questionnaireName');
        // console.log(this.questionnaireTemplateId, 'questionnaireTemplateId');
      },
      changeStatus(id, status) {
        this.changeQuestionnaireEachStatus({id, status}).then((data) => {
          if(data) {
            this.getQuestionnaireEachList();
            this.$notify({
              title: '成功',
              message: '更新成功',
              type: 'success'
            });
          }else {
            this.$notify({
              title: '警告',
              message: '开启新问卷前，请先关闭同一模板下的其他问卷。',
              type: 'warning'
            });
          }

        });
      }
    },
    components: {

    },
    computed: {
      ...mapState({
        QuestionnaireEach: state => state.QuestionnaireEach,
        QuestionnaireTemplate: state => state.QuestionnaireTemplate,
      }),
    },
    mounted() {
      this.init();
    }
  }
</script>

<style>
  /*#row-1 {
    height: 650px;
  }
  #f-div {
    padding-left: 10px;
  }*/
</style>



