<template>
  <div>
    <el-row class="row-padding" type="flex" justify="center" align="middle" >
      <el-col :span="4">
        <el-steps :space="100" :active="active" finish-status="success">
          <el-step title="步骤 1"></el-step>
          <el-step title="步骤 2"></el-step>
          <el-step title="步骤 3"></el-step>
        </el-steps>
      </el-col>
    </el-row>
    <el-row v-if="active === 0" class="row-padding" type="flex" justify="center" align="middle" >
      <el-col :span="5"><el-input v-model="name" placeholder="问卷模板名称"></el-input></el-col>
    </el-row>
    <el-row v-if="active === 1" class="row-padding" type="flex" justify="center" align="middle" >
      <el-col>
        <el-row class="row-padding" type="flex" justify="left" align="middle" >
          <h2>题目设置：</h2>
        </el-row>
        <el-row type="flex" justify="center" align="middle" >
          <el-col :span="8">
            <el-input type="textarea" :rows="3" placeholder="请输入题目" v-model="questionName"></el-input>
          </el-col>
          <el-col :span="3" :offset="1">
            <el-radio class="radio" v-model="multiple" label="1">单选</el-radio>
            <el-radio class="radio" v-model="multiple" label="2">多选</el-radio>
          </el-col>
          <el-col :span="5">
            <span>是否可打分</span>
            <el-switch
              v-model="grade"
              on-color="#13ce66"
              off-color="#ff4949">
            </el-switch>
          </el-col>
        </el-row>
        <el-row class="row-padding" type="flex" justify="left" align="middle" >
          <h2>题目设置：</h2>
        </el-row>
        <el-row type="flex" justify="center" align="middle" >
          <el-col :span="8">
            <el-input placeholder="答案" v-model="questionName"></el-input>
          </el-col>
          <el-col :span="4" :offset="1">
            <span>是否可添加文字</span>
            <el-switch
              v-model="grade"
              on-color="#13ce66"
              off-color="#ff4949">
            </el-switch>
          </el-col>
          <el-col :span="3" :offset="1">
            <el-button  type="success" icon="plus" class="addLable"></el-button>
            <el-button type="danger" icon="minus" class="addLable"></el-button>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <el-row class="row-padding" type="flex" justify="center" align="middle" >
      <el-col :span="2">
        <el-button style="margin-top: 12px;" @click="back">上一步</el-button>
      </el-col>
      <el-col :span="1">
        <el-button style="margin-top: 12px;" @click="next">下一步</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
  // import _ from 'lodash';
  import { mapActions, mapState } from 'vuex';

  export default {
    name: 'SetQuestion',
    data(){
      return {
        active: 0,
        name: '',
        questionName: '',
        multiple: '1',
        grade: false,
      }
    },
    methods: {
      ...mapActions(['getQuestionList']),
      init() {
        this.getQuestionList();
        // console.log(app, '12312312312');
      },
      back() {
        if (this.active-- === 0) this.active = 0;
      },
      next() {
        console.log(this.active, 'active');
        if (this.active++ > 2) this.active = 0;
      }
    },
    components: {

    },
    computed: {
      ...mapState({
        Question: state => state.Question,
      }),
    },
    mounted() {
      this.init();
    }
  }
</script>

<style>
  .row-padding {
    padding: 20px;
  }

</style>



